@import 'utils/mixins';
// Ionic Variables and Theming. For more info, please see:
// http://ionicframework.com/docs/theming/

/** Ionic CSS Variables **/
:root {
    /** Font **/
    --ion-font-family: Segoe UI, -apple-system, system-ui, Roboto, Oxygen, Ubuntu, Fira Sans, Droid Sans, Helvetica Neue,
        sans-serif;

    /* container */
    --container-size: 1190px;

    /* header */
    --header-mobile-size: 3.75rem;

    /** primary **/
    --ion-color-primary: #7a65e1;
    --ion-color-primary-rgb: 122, 101, 225;
    --ion-color-primary-contrast: #ffffff;
    --ion-color-primary-contrast-rgb: 255, 255, 255;
    --ion-color-primary-shade: #6654bc;
    --ion-color-primary-tint: #8774e4;

    /** secondary **/
    --ion-color-secondary: #ebefff;
    --ion-color-secondary-rgb: 235, 239, 255;
    --ion-color-secondary-contrast: #000000;
    --ion-color-secondary-contrast-rgb: 0, 0, 0;
    --ion-color-secondary-shade: #cfd2e0;
    --ion-color-secondary-tint: #edf1ff;

    /** tertiary **/
    --ion-color-tertiary: #5260ff;
    --ion-color-tertiary-rgb: 82, 96, 255;
    --ion-color-tertiary-contrast: #ffffff;
    --ion-color-tertiary-contrast-rgb: 255, 255, 255;
    --ion-color-tertiary-shade: #4854e0;
    --ion-color-tertiary-tint: #6370ff;

    /** success **/
    --ion-color-success: #96d055;
    --ion-color-success-rgb: 150, 208, 85;
    --ion-color-success-contrast: #000000;
    --ion-color-success-contrast-rgb: 0, 0, 0;
    --ion-color-success-shade: #6cbf10;
    --ion-color-success-tint: #a1d566;

    /** warning **/
    --ion-color-warning: #ffa500;
    --ion-color-warning-rgb: 255, 170, 47;
    --ion-color-warning-contrast: #000000;
    --ion-color-warning-contrast-rgb: 0, 0, 0;
    --ion-color-warning-shade: #e09629;
    --ion-color-warning-tint: #ffb344;

    /** danger **/
    --ion-color-danger: #ff6767;
    --ion-color-danger-rgb: 255, 103, 103;
    --ion-color-danger-contrast: #000000;
    --ion-color-danger-contrast-rgb: 0, 0, 0;
    --ion-color-danger-shade: #e05b5b;
    --ion-color-danger-tint: #ff7676;

    /** dark **/
    --ion-color-dark: #222428;
    --ion-color-dark-rgb: 34, 36, 40;
    --ion-color-dark-contrast: #ffffff;
    --ion-color-dark-contrast-rgb: 255, 255, 255;
    --ion-color-dark-shade: #1e2023;
    --ion-color-dark-tint: #383a3e;

    /** medium **/
    --ion-color-medium: #9b9b9b;
    --ion-color-medium-rgb: 155, 155, 155;
    --ion-color-medium-contrast: #000000;
    --ion-color-medium-contrast-rgb: 0, 0, 0;
    --ion-color-medium-shade: #828282;
    --ion-color-medium-tint: #4a4a4a;

    /** light **/
    --ion-color-light: #ffffff;
    --ion-color-light-gray: #f5f5fa;
    --ion-color-light-rgb: 255, 255, 255;
    --ion-color-light-contrast: #000000;
    --ion-color-light-contrast-rgb: 0, 0, 0;
    --ion-color-light-shade: #e0e0e0;
    --ion-color-light-tint: #ffffff;
  /** light **/
  --ion-color-light: #ffffff;
  --ion-color-light-rgb: 255,255,255;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0,0,0;
  --ion-color-light-shade: #e0e0e0;
  --ion-color-light-tint: #ffffff;

}

@include mobile {
    :root {
        --ion-font-family: 'Roboto', sans-serif;
    }
}
